* {
    --sidebar-width: 280px;
    --content-margin-x: 20px;
    --content-margin-y: 5px;
    --content-width-with-margin: calc(100vw - var(--sidebar-width) - 2 * var(--content-margin-x));
    --content-width-without-margin: calc(100vw - var(--sidebar-width));

    --font-color: #ffb30d;
    --dark-blue: #001542;
    --button-green: #085454;
}

body {
    width: 100vw;
    height: 100vh;
}
